import { ApproveService } from './../../../approve-course/approve.service';
import { RejectComponent } from './../../../confirm-delete/components/dialogs/reject/reject.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmComponent } from '../dialogs/confirm/confirm.component';
import { environment } from '../../../../../environments/environment';
import { AppInitService } from 'src/app/service/app-init.service';

@Component({
  selector: 'app-confirm-delete',
  templateUrl: './confirm-delete.component.html',
  styleUrls: ['./confirm-delete.component.scss'],
})
export class ConfirmDeleteComponent implements OnInit {
  displayedColumns: string[] = [
    'video',
    'name',
    'category',
    'uploader',
    'length',
    'price',
    'status',
    'opration',
  ];

  public course: COURSE;
  courses: COURSE[];
  category;
  categories: string[];
  constructor(public dialog: MatDialog, public services: ApproveService) {
    // this.course = {
    //   id: "course A",
    //   name: 'Course Example',
    //   price: 320,
    //   length: "3:22:12",
    //   author: 'mohammaddargoei',
    // }
  }

  ngOnInit(): void {
    this.coursesList(undefined);
    this.categoriesList();
  }
  coursesList(category?: string) {
    this.services
      .courseList('deleterequest', undefined, category)
      .subscribe((res) => {
        let data = res.courses;
        console.log(data, res);
        this.courses = [];
        for (let file of data) {
          this.courses.push({
            id: file?._id,
            video: `${AppInitService.baseUrl}/${file?.file}`,
            name: file?.name,
            category: file?.category,
            uploader: file?.owner?.firstName + ' ' + file?.owner?.lastName,
            description: file?.description,
            price: file?.price,
            length: new Date(file?.length * 1000).toISOString().substr(14, 5),
            status: this.convertStatus(file.status),
            poster: `${AppInitService.baseUrl}/${file.thumbnail}`,
            notes: file?.notes ? file?.notes : [],
          });
        }
      });
  }
  categoriesList() {
    this.services.categoryList().subscribe((res) => {
      this.categories = res.categories;
    });
  }
  changeCategory(e) {
    this.coursesList(e);
  }
  openConfirm(course) {
    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '550px',
      panelClass: 'dialog-confirm-course',
      data: course,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log('The dialog was closed');
    });
  }
  convertStatus(status): number {
    let st;
    switch (status) {
      case 'pending':
        st = 1;
        break;
      case 'approved':
        st = 2;
        break;
      case 'rejected':
        st = 3;
        break;
      case 'deleterequest':
        st = 4;
        break;
      default:
        st = 1;
        break;
    }
    return st;
  }
}

export interface COURSE {
  id: any;
  video: string;
  name: string;
  category: string;
  uploader: string;
  description?: string;
  length: string;
  price: number;
  status: number;
  poster?: string;
  notes?: any;
}
